<template lang="pug">
  .submenu
    .container
      .submenu__wrapper
        .submenu__logo
          slot(name="header")
        .submenu__menu
          slot(name="right")
</template>

<script>
export default {
  name: 'aes-submenu'
}
</script>

<style lang="less">
@submenu-height: 60px;

.submenu {
  border-bottom: 1px solid #cccbcb;
  height: @submenu-height;
  background-color: #fff;
}
.submenu__wrapper {
  display: flex;
  flex-direction: row;
  height: @submenu-height;
}
.submenu__logo {
  min-width: 0;
  align-self: center;
  font-family: 'Asap', 'Open Sans', sans-serif;
  h1 {
    font-size: 22px;
    font-weight: 700;
    margin: 0;
  }
}
.submenu__menu {
  margin-left: auto;
  align-self: center;
  ul {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
    list-style-type: none;
    li {
      margin-left: 25px;
      a {
        color: #696969;
        &:hover {
          text-decoration: none;
          color: #18a3fa;
        }
        &.aes-button {
          background-color: rgba(24, 163, 250, 0.85);
          border-radius: 20px;
          padding: 5px 15px;
          color: #fff;
          &:hover {
            background-color: rgba(24, 163, 250, 1);
          }
        }
      }
    }
  }
}
</style>



